body {
  margin: 0;
  /* padding: 20px; */
  height: 100vh;
  background-color: #1e2024;
  line-height: 26px;
}

.wrapper {
  padding: 0 2rem;
  font-family: source sans pro,sans-serif;
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}

.liveStream {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: 100%
}

.liveStream .event {
  margin-bottom: 1rem
}

.liveStream .event .title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fc0
}

.liveStream .event .date {
  color: rgba(255,255,255,.596)
}

.liveStream .video {
  border-radius: 5px;
  width: 100%
}

.liveStream .event .titleMessage {
  font-size: 1.5rem;
  font-weight: 700;
  color: red
}

.play {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  padding: 1rem 0 0 2rem
}

.play .bettingStatus {
  background: #b10f0f;
  padding: .3rem;
  border-radius: 5px;
  text-align: center
}

.play .currentGame {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem
}

.play .currentGame th {
  text-transform: uppercase
}

.play .currentGame td,.play .currentGame th {
  border: 1px solid rgba(255,255,255,.13);
  padding: .5rem;
  text-align: center;
  width: 50%;
  color: #fff
}

.play .bettingStatus.active {
  background: #23b04b
}

.play .bets {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  background-color: #1e2024
}

.play .bets .betMeron {
  border: 1px solid rgb(254 239 41/0.30)
}

.play .bets .betMeron .info button {
  background-color: #23b04b;
  border: 1px solid #23b04b
}

.play .bets .betMeron .info button:hover {
  color: #fff;
  background-color: #f34141;
  border: 1px solid #fff
}

.play .bets .betWala {
  border: 1px solid rgb(254 239 41/0.30)
}

.play .bets .betWala .info button {
  background-color: #23b04b;
  border: 1px solid #23b04b
}

.play .bets .betWala .info button:hover {
  color: #fff;
  background-color: #1d81f1;
  border: 1px solid #fff
}

.play .bets .betMeron .title {
  background: #f34141
}

.play .bets .betWala .title {
  background: #1d81f1
}

.play .bets>div {
  width: 100%;
  border: 1px solid rgba(255,255,255,.11);
  overflow: hidden;
  border-radius: 5px
}

.play .bets>div .title {
  padding: .8rem 0;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center
}

.play .bets>div .info {
  padding-left: 2px;
  padding-right: 2px;
  padding-bottom: 2px
}




.play .bets>div .info .currentBet span.bet-win {
  text-align: center;
  font-weight: 600;
  font-size: 3rem;
  color: #fc0;
  padding: 1px;
  border-radius: 4px;
  margin: 0
}

.play .bets>div .info button {
  padding: 1rem 2rem;
  background: 0 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  border: none;
  color: #fff;
  background: #23b04b;
  border-radius: 5px;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: 100%
}

.play .currentPoints {
  margin-top: 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 1rem
}

.play .currentPoints p:nth-child(2) {
  color: #fc0;
  font-size: 20px;
  font-weight: 700
}

.play .currentPoints p:nth-child(2) i {
  padding-right: .3rem
}

.play .amount {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #000;
  border-radius: 5px;
  margin-top: 1rem
}

.play .amount input[type=text] {
  font-weight: 400;
  background: #1d2833;
  border: none;
  font-size: 15px;
  padding: 1rem;
  border-radius: 3px;
  color: #fff;
  outline: none;
  position: relative;
  resize: none;
  width: 100%
}

.play .amount button {
  padding: 1rem 2rem;
  background: 0 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  border: none;
  color: #fff;
  border-left: 1px solid #2f3238
}

.play .amount button:hover {
  background: #363b40
}

.play .amounts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 1rem;
  width: 100%;
  overflow: auto;
  padding: 2rem 0;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between
}

.play .amountsBet {
  width: 100%;
  overflow: auto;
  padding: 2rem 0;
  text-align: center
}

.play .amounts button {
  border-radius: 5px;
  background: #282b30;
  cursor: pointer;
  border: none;
  color: #fff
}

.play .amounts button:hover {
  background: #363b40
}

.play .betDraw {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem
}

.play .betDraw button {
  padding: 1rem 2rem;
  background: 0 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  border: none;
  color: #fff;
  background: #23b04b;
  border-radius: 5px;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1
}

.play .betDraw .drawed {
  padding: 1rem 2rem;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  text-align: center
}

.play .betDraw .drawedTotal {
  padding: 1rem 2rem;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  text-align: center;
  color: #23b04b
}

.play .note {
  color: rgba(255,255,255,.308);
  margin-top: 1rem;
  letter-spacing: 1px
}

.play .note i {
  margin-right: .5rem
}

.play .results {
  width: 100%;
  margin: 1rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column
}

.play .results .currentBets {
  max-width: 720px;
  overflow-x: auto;
  max-height: 300px;
  margin: 2rem 0
}

.play .results .currentBets table {
  border-collapse: collapse;
  background: #fff
}

.play .results .currentBets table tr td {
  padding: 2.25px;
  border: 1px solid rgba(0,0,0,.178)
}

.play .results .currentBets table tr td div {
  height: 2.5rem;
  width: 2.5rem
}

.play .results .currentBets table tr td .circleRed {
  height: 2.5rem;
  width: 2.5rem;
  background-color: #fff;
  border-radius: 1.2rem;
  border: 8px solid #f34141
}

.play .results .currentBets table tr td .circleBlue {
  height: 2.5rem;
  width: 2.5rem;
  background-color: #fff;
  border-radius: 1.2rem;
  border: 8px solid #1d81f1
}

.play .results .currentBets table tr td .circleGreen {
  height: 2.5rem;
  width: 2.5rem;
  background-color: #fff;
  border-radius: 1.2rem;
  border: 8px solid #23b04b
}

.play .results .currentBets table tr td .circleRedAll {
  height: 2.5rem;
  width: 2.5rem;
  background-color: #f34141;
  border-radius: 1.2rem;
  text-align: center;
  color: #fff;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}

.play .results .currentBets table tr td .circleBlueAll {
  display: flex;
  height: 2.5rem;
  width: 2.5rem;
  background-color: #1d81f1;
  border-radius: 1.2rem;
  text-align: center;
  color: #fff;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}

.play .results .currentBets table tr td .circleGreenAll {
  height: 2.5rem;
  width: 2.5rem;
  background-color: #23b04b;
  border-radius: 1.2rem;
  text-align: center;
  color: #fff;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}

.play .results .currentBets table tr td .circleCancelAll {
  height: 2.5rem;
  width: 2.5rem;
  background-color: rgba(0,0,0,.178);
  border-radius: 1.2rem;
  text-align: center;
  color: #000;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}

.play .results .currentBets table tr td:nth-child(even) .circle {
  border: 8px solid #1830b9
}

.play .results .resultLabel {
  width: 100%;
  max-width: 600px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  margin: 1rem
}

.play .resultLabel>a {
  background: 0 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  border: 1px solid #dadada;
  color: #dadada;
  background-color: #373838;
  border-radius: 5px;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  padding: .1rem 1rem
}

.play .results .resultLabel>div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: .5rem
}

.play .results .resultLabel>div .circle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.8rem;
  width: 1.8rem;
  border-radius: 2rem;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px
}

.play .results .resultLabel>div .circle.meron {
  background: #f34141;
  padding: 20px
}

.play .results .resultLabel>div .circle.wala {
  background: #1d81f1;
  padding: 20px
}

.play .results .resultLabel>div .circle.draw {
  background: #23b04b;
  padding: 20px
}

.play .results .resultLabel>div .circle.cancel {
  background: rgba(252,252,252,.39);
  padding: 20px
}

.play .results .currentBets {
  max-width: 720px;
  overflow-x: auto;
  max-height: 300px;
  margin: 2rem 0
}

.play .results .currentBets .circle ul li.meron {
  background: #b10f0f;
  padding: 10px
}

.play .results .currentBets .circle ul li.wala {
  background: #1830b9;
  padding: 10px
}

.play .results .currentBets .circle ul li.draw {
  background: #23b04b;
  padding: 10px
}

.play .results .currentBets .circle ul li.cancel {
  background: rgba(252,252,252,.39);
  padding: 10px
}

.play .results .result {
  width: 100%;
  overflow: auto;
  max-height: 300px
}

.play .results .result table {
  border-collapse: collapse;
  margin: 1rem 0;
  background: #fff
}

.play .results .result table tr td {
  padding: 7px;
  border: 1px solid rgba(0,0,0,.178)
}

.play .results .result table tr td .circle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.7rem;
  width: 1.7rem;
  border-radius: 1rem;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center
}

.play .results .result table tr td .circle.meron {
  background: #b10f0f
}

.play .results .result table tr td .circle.wala {
  background: #1830b9
}

.play .results .result table tr td .circle.draw {
  background: #23b04b
}

.play .results .result table tr td .circle.cancelled {
  background: rgba(252,252,252,.39)
}

@media(max-width: 1200px) {
  .wrapper {
      -webkit-box-orient:vertical;
      -webkit-box-direction: normal;
      -ms-flex-direction: column;
      flex-direction: column
  }

  .play {
      padding: 0
  }

  .play .results .currentBets {
      max-width: 720px;
      overflow-x: auto;
      max-height: 300px;
      margin: 2rem 0
  }
}

@media(max-width: 600px) {
  .wrapper {
      padding:1rem
  }

  .play .results .currentBets {
      max-width: 600px;
      overflow-x: auto;
      max-height: 300px;
      margin: 2rem 0
  }
}

.blink_me {
  animation: blinker 10s linear infinite
}

