  padding-left: 6px;
  padding-right: 6px;
  padding-top: 3px;
  padding-bottom: 3px;
  border-radius: 2px
}

.text-open-bet {
  color: #fff;
  font-weight: 700;
  font-size: 15px;
  background-color: #2eb85c;
  padding-left: 6px;
  padding-right: 6px;
  padding-top: 3px;
  padding-bottom: 3px;
