.play .amount {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #000;
    border-radius: 5px;
    margin-top: 1rem
}

.play .amount input[type=text] {
    font-weight: 400;
    background: #1d2833;
    border: none;
    font-size: 15px;
    padding: 1rem;
    border-radius: 3px;
    color: #fff;
    outline: none;
    position: relative;
    resize: none;
    width: 100%
}

.play .amount button {
    padding: 1rem 2rem;
    background: 0 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    cursor: pointer;
    border: none;
    color: #fff;
    border-left: 1px solid #2f3238
}

.play .amount button:hover {
