}

.play .betDraw {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem
}

.play .betDraw button {
    padding: 1rem 2rem;
    background: 0 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    cursor: pointer;
    border: none;
    color: #fff;
    background: #23b04b;
    border-radius: 5px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1
}

.play .betDraw .drawed {
    padding: 1rem 2rem;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: center
}

.play .betDraw .drawedTotal {
    padding: 1rem 2rem;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: center;
