    overflow-x: auto;
    max-height: 300px;
    margin: 2rem 0
}

.play .results .currentBets table {
    border-collapse: collapse;
    background: #fff
}

.play .results .currentBets table tr td {
    padding: 2.25px;
    border: 1px solid rgba(0,0,0,.178)
}

.play .results .currentBets table tr td div {
    height: 2.5rem;
    width: 2.5rem
}

.play .results .currentBets table tr td .circleRed {
    height: 2.5rem;
    width: 2.5rem;
    background-color: #fff;
    border-radius: 1.2rem;
    border: 8px solid #f34141
}

.play .results .currentBets table tr td .circleBlue {
    height: 2.5rem;
    width: 2.5rem;
    background-color: #fff;
    border-radius: 1.2rem;
    border: 8px solid #1d81f1
}

.play .results .currentBets table tr td .circleGreen {
    height: 2.5rem;
    width: 2.5rem;
    background-color: #fff;
    border-radius: 1.2rem;
    border: 8px solid #23b04b
}

.play .results .currentBets table tr td .circleRedAll {
    height: 2.5rem;
    width: 2.5rem;
    background-color: #f34141;
    border-radius: 1.2rem;
    text-align: center;
    color: #fff;
