    height: 2.5rem;
    width: 2.5rem;
    background-color: rgba(0,0,0,.178);
    border-radius: 1.2rem;
    text-align: center;
    color: #000;
    font-weight: 700
}

.play .results .currentBets table tr td:nth-child(even) .circle {
    border: 8px solid #1830b9
}

.play .results .resultLabel {
    width: 100%;
    max-width: 600px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: space-evenly;
    -ms-flex-pack: space-evenly;
    justify-content: space-evenly;
