.play .results .resultLabel>div {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    gap: .5rem
}

.play .results .resultLabel>div .circle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 1.8rem;
    width: 1.8rem;
    border-radius: 2rem;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 10px
}

.play .results .resultLabel>div .circle.meron {
    background: #f34141;
    padding: 20px
}

.play .results .resultLabel>div .circle.wala {
    background: #1d81f1;
    padding: 20px
}

.play .results .resultLabel>div .circle.draw {
    background: #23b04b;
    padding: 20px
}

.play .results .resultLabel>div .circle.cancel {
    background: rgba(252,252,252,.39);
    padding: 20px
}

.play .results .currentBets {
    max-width: 720px;
    overflow-x: auto;
    max-height: 300px;
    margin: 2rem 0
}

.play .results .currentBets .circle ul li.meron {
    background: #b10f0f;
    padding: 10px
}

.play .results .currentBets .circle ul li.wala {
    background: #1830b9;
    padding: 10px
}

.play .results .currentBets .circle ul li.draw {
    background: #23b04b;
    padding: 10px
}

.play .results .currentBets .circle ul li.cancel {
    background: rgba(252,252,252,.39);
    padding: 10px
}

.play .results .result {
    width: 100%;
    overflow: auto;
    max-height: 300px
}

.play .results .result table {
    border-collapse: collapse;
    margin: 1rem 0;
    background: #fff
}

.play .results .result table tr td {
    padding: 7px;
    border: 1px solid rgba(0,0,0,.178)
}

.play .results .result table tr td .circle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 1.7rem;
    width: 1.7rem;
    border-radius: 1rem;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center
}

.play .results .result table tr td .circle.meron {
    background: #b10f0f
}

.play .results .result table tr td .circle.wala {
    background: #1830b9
}

.play .results .result table tr td .circle.draw {
