@media(max-width: 1200px) {
    .wrapper {
        -webkit-box-orient:vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column
    }

    .play {
        padding: 0
    }

    .play .results .currentBets {
        max-width: 720px;
        overflow-x: auto;
        max-height: 300px;
        margin: 2rem 0
    }
}

@media(max-width: 600px) {
    .wrapper {
        padding:1rem
    }

    .play .results .currentBets {
        max-width: 600px;
        overflow-x: auto;
        max-height: 300px;
        margin: 2rem 0
    }
}

