.marquee {
    height: 30px;
    overflow: hidden;
    position: relative;
    background: #b31013;
    color: #fff
}

.marquee p {
    white-space: nowrap;
    position: absolute;
    font-weight: 700;
    font-size: 18px;
    width: 100%;
    height: 100%;
    margin: 0;
    line-height: 30px;
    text-align: center;
    -moz-transform: translateX(100%);
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    -moz-animation: scroll-left 14s linear infinite;
    -webkit-animation: scroll-left 14s linear infinite;
    animation: scroll-left 14s linear infinite
}

@-moz-keyframes scroll-left {
    0% {
        -moz-transform: translateX(100%)
    }

    100% {
        -moz-transform: translateX(-300%)
    }
}

@-webkit-keyframes scroll-left {
    0% {
        -webkit-transform: translateX(100%)
    }

    100% {
        -webkit-transform: translateX(-300%)
    }
}

@keyframes scroll-left {
    0% {
        -moz-transform: translateX(100%);
        -webkit-transform: translateX(100%);
        transform: translateX(100%)
    }

    100% {
        -moz-transform: translateX(-300%);
        -webkit-transform: translateX(-300%);
        transform: translateX(-300%)
    }
}

#box-history-automatic {
    position: relative;
    height: 190px;
    overflow-x: scroll;
    overflow-y: hidden;
    background-color: #ecf0f1
}

#box-history-automatic .badge,.box-history-automatic .badge {
    box-shadow: -1px 1px 1px 1px rgba(0,0,0,.5);
    -moz-box-shadow: -1px 1px 1px 1px rgba(0,0,0,.5);
    -webkit-box-shadow: -1px 1px 1px 1px rgba(0,0,0,.5);
    -o-box-shadow: -1px 1px 1px 1px rgba(0,0,0,.5);
    -moz-border-radius: 30px;
    -webkit-border-radius: 30px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    font-size: 11px;
    font-weight: 700;
    margin-left: 2px;
    margin-right: auto;
    font-family: montserrat,sans-serif!important
}

#box-history-automatic .badge-primary,.box-history-automatic .badge-primary {
    background-color: #0b4592
}

#box-history-automatic .badge-danger,.box-history-automatic .badge-danger {
    background-color: #b31013
}

#box-history-automatic table tr td,.box-history-automatic table tr td {
    max-width: 32px;
    min-width: 35px;
    max-height: 30px;
    min-height: 30px;
    height: 30px;
    padding: 2px 3px 1px;
    background-color: #ecf0f1;
    color: #fff;
    border: 1px solid #dedede
}

#box-history-automatic table tr td span.badge {
    margin: 0;
    margin-left: 0
}

#box-history-automatic .badge,.box-history-automatic .badge {
    padding: 10px 2px
