
@font-face {
    font-family: 'Source Code Pro';
    font-style: normal;
    font-weight: 400;
     }
  
  
  
  .clock {
      margin: 0 auto;
     padding-top:5px;
      color: #fff;
      height: 30px !important;
      font-size: 16px;
  }
  
  #Date {
      font-family: 'Source Code Pro', Arial, Helvetica, sans-serif;
      font-size: 16px;
      text-align: center;
      text-shadow: 0 0 5px #00c6ff;
     
  }
  
  .clock ul {
      margin: 0 auto;
      padding: 0px;
      list-style: none;
      font-weight:bold;
      text-align: center;
  }
  
  .clock ul li {
      display: inline;
      font-size: 1em;
      text-align: center;
      font-family: 'Source Code Pro', Arial, Helvetica, sans-serif;
      text-shadow: 0 0 5px #00c6ff;
       font-weight:bold;
  }
  
  #point {
      position: relative;
      -moz-animation: mymove 1s ease infinite;
      -webkit-animation: mymove 1s ease infinite;
      padding-left: 10px;
      padding-right: 10px;
  }
  
  /* Simple Animation */
  @-webkit-keyframes mymove {
      0% {opacity: 1.0;
      text-shadow: 0 0 20px #00c6ff;
  }
  
  50% {
      opacity: 0;
      text-shadow: none;
  }
  
  100% {
      opacity: 1.0;
      text-shadow: 0 0 20px #00c6ff;
  }	
  }
  
  @-moz-keyframes mymove {
      0% {
          opacity: 1.0;
          text-shadow: 0 0 20px #00c6ff;
      }
  
      50% {
          opacity: 0;
          text-shadow: none;
      }
  
      100% {
          opacity: 1.0;
          text-shadow: 0 0 20px #00c6ff;
      };
  }